.container {
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
    height: 400px;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
}

.container img {
    width: 50px;
    margin: 5px;
    margin-top: 20px;
    border-radius: 50px;
}

.container h3 {
    margin-bottom: 15px;
    color: #2a588b;
    font-size: 15px;
}

.item{
    background-color:#dae1e8;
    width: 120px;
    height: 120px;
    margin: 5px;
}